<template>
  <div class="table">
    <table class="mk-table text-center">
      <thead>
        <tr>
          <th>下单日期</th>
          <th>订单编号</th>
          <th>订单状态</th>
          <th>商家</th>
          <th>物料数(种)</th>
          <th>金额</th>
          <th>收货人</th>
          <th>联系电话</th>
          <th>操作</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in data">
          <td>{{item.OrderDate}}</td>
          <td>{{item.OrderCode}}</td>
          <td>{{item.OStatus | status }}</td>
          <td>{{item.ShopName}}</td>
          <td>{{item.Qty}}</td>
          <td>￥{{item.NeedPayAmount}}</td>
          <td>{{item.CSGName}}</td>
          <td>{{item.CSGMobile}}</td>
          <td>
            <a href="">查看详情</a>
            <a href="">再来一单</a>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="page">
      <Page :total="total" :page-size="5" @on-change="changePage" ></Page>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'BuyHistory',
    data() {
      return {
        data: [],
        total: 0
      }
    },
    filters: {
      status(data) {
        const map = {
          '$0': '待审核',
          '$1': '待发货',
          '$2': '待收货',
          '$3': '交易完成',
          '$-2': '已取消',
          '$-6': '待付款'
        }
        // --0:待审核;1:待发货;2:待收货;3:交易完成;-2已取消;-6:待付款"
        return map[`$${data}`]
      }
    },
    mounted() {
      this.getData()
    },
    methods: {
      getData(page) {
        return window.$$action({
          module: 'index_getBuyHistory',
          api: 'index_getBuyHistory',
          params: {
            pageIndex: page||0
          }
        }).then(res => {
          this.data = res.Msg
          if (this.data && this.data[0]) {
            this.total = res.Msg[0].Rows/1
          }
        })
      },
      changePage(page) {
        this.getData(page-1)
      }
    }
  }

</script>
<style lang="less" scoped>
  .table {
    padding: 20px 10px;
    .page {
      padding: 10px 20px;
      text-align: right
    }
  }

</style>



