<template>
  <div class="sidebarct">
    <Menu @on-select="selectMenu" ref="Menu" theme="light" class="menuct" :active-name="active" :open-names="openname" width="auto">
      <Submenu name="1">
        <template slot="title">
          <Icon class="sidebaricon" type="ios-home"></Icon>
          首页
        </template>
        <MenuItem name="1-1">首页</MenuItem>
        <MenuItem name="1-2">意见反馈</MenuItem>
        <MenuItem name="1-3">公告</MenuItem>
      </Submenu>
      <Submenu name="2">
        <template slot="title">
          <Icon class="sidebaricon" type="ios-cart"></Icon>
          我要下单
        </template>
        <MenuItem name="2-1">我要下单</MenuItem>
        <MenuItem name="2-2">我的购物车</MenuItem>
      </Submenu>
      <Submenu name="3">
        <template slot="title">
          <Icon class="sidebaricon" type="ios-list"></Icon>
          订单管理
        </template>
        <MenuItem name="3-1">我的订单</MenuItem>
        <MenuItem name="3-2">订单报表</MenuItem>
        <MenuItem name="3-3">出库查询</MenuItem>
        <MenuItem name="3-4">退货管理</MenuItem>
      </Submenu>
      <Submenu name="4">
        <template slot="title">
          <Icon class="sidebaricon" type="ios-printer"></Icon>
          开票管理
        </template>
        <MenuItem name="4-1">开票申请</MenuItem>
        <MenuItem name="4-2">我的开票信息</MenuItem>
      </Submenu>
      <Submenu name="5">
        <template slot="title">
          <Icon class="sidebaricon" type="ios-paper"></Icon>
          账单管理
        </template>
        <MenuItem name="5-1">在线回款</MenuItem>
        <MenuItem name="5-2">回款认领</MenuItem>
        <MenuItem name="5-3">往来明细账</MenuItem>
      </Submenu>
      <Submenu name="6">
        <template slot="title">
          <Icon class="sidebaricon" type="ios-filing"></Icon>
          我的账户
        </template>
        <MenuItem name="6-1">账户信息</MenuItem>
        <MenuItem name="6-2">我的信用额度</MenuItem>
        <MenuItem name="6-3">三证上传</MenuItem>
        <MenuItem name="6-4">收货地址</MenuItem>
        <MenuItem name="6-5">修改密码</MenuItem>
      </Submenu>
      <Submenu name="7">
        <template slot="title">
          <Icon class="sidebaricon" type="ios-folder"></Icon>
          证书管理
        </template>
        <MenuItem name="7-1">产品注册证</MenuItem>
        <MenuItem name="7-2">授权证书</MenuItem>
        <MenuItem name="7-3">法人委托书</MenuItem>
        <MenuItem name="7-4">销售承诺书</MenuItem>
        <MenuItem name="7-5">证书过期提醒</MenuItem>
      </Submenu>
    </Menu>
  </div>
</template>
<script>
  import vue from 'vue'
  const routeMap = [
    {
      name: '1',
      title: '首页',
      children: [
        {
          name: '1-1',
          title: '首页'
        },{
          name: '1-2',
          title: '意见反馈'
        },{
          name: '1-3',
          title: '公告'
        }
      ]
    },{
      name: '2',
      title: '我要下单',
      children: [
        {
          name: '2-1',
          title: '我要下单'
        },{
          name: '2-2',
          title: '我的购物车'
        }
      ]
    },{
      name: '3',
      title: '订单管理',
      children: [
        {
          name: '3-1',
          title: '我的订单'
        },{
          name: '3-2',
          title: '订单报表'
        },{
          name: '3-3',
          title: '出库查询'
        },{
          name: '3-4',
          title: '退货管理'
        }
      ]
    },{
      name: '4',
      title: '开票管理',
      children: [
        {
          name: '4-1',
          title: '开票申请'
        },{
          name: '4-2',
          title: '我的开票信息'
        }
      ]
    },{
      name: '5',
      title: '账单管理',
      children: [
        {
          name: '5-1',
          title: '在线回款'
        },{
          name: '5-2',
          title: '回款认领'
        },{
          name: '5-3',
          title: '往来明细账'
        }
      ]
    },{
      name: '6',
      title: '我的账户',
      children: [
        {
          name: '6-1',
          title: '账户信息'
        },{
          name: '6-2',
          title: '我的信用额度'
        },{
          name: '6-3',
          title: '三证上传'
        },{
          name: '6-4',
          title: '收货地址'
        },{
          name: '6-5',
          title: '修改密码'
        }
      ]
    },{
      name: '7',
      title: '证书管理',
      children: [
        {
          name: '7-1',
          title: '产品注册证'
        },{
          name: '7-2',
          title: '授权证书'
        },{
          name: '7-3',
          title: '法人委托书'
        },{
          name: '7-4',
          title: '销售承诺书'
        },{
          name: '7-5',
          title: '证书过期提醒'
        }
      ]
    }
  ]
  export default {
    name: 'SideBar',
    data() {
      return {
        openname: [],
        active: ''
      }
    },
    mounted() {
      this.init()
    },
    methods: {
      selectMenu(name) {
        this.$router.push({name})
      },
      init() {
        this.active = this.$route.name
        this.openname.splice(0,5,this.$route.meta.menuOpen)
        this.$refs.Menu.updateActiveName();
        this.$refs.Menu.updateOpened();
      }
    }
  }

</script>

<style lang="less" scoped>
  .sidebarct {
    background-color: #fff;
    height: 100%;
  }
  .sidebaricon {
    font-size: 20px;
    position: relative;
    top: 2px;
  }
  .menuct {
    height: 100%;
  }
</style>



